"use client";

import { useEffect } from "react";
import Lenis from "@studio-freight/lenis";
import Navbar from "../../components/Navbar";
import Brandlabels from "@/components/ui/Brandlabels";

export default function Home() {
  useEffect(() => {
    // Initialize Lenis
    const lenis = new Lenis({
      duration: 1.5,
      easing: (t) => t, // linear easing
      orientation: "vertical",
      smoothWheel: true,
      lerp: 0.1,
    });

    // Animation frame loop
    const raf = (time: number) => {
      lenis.raf(time);
      requestAnimationFrame(raf);
    };

    requestAnimationFrame(raf);

    // Optional: scroll to top on page load
    lenis.scrollTo(0);
  }, []);

  return (
    <div className="min-h-screen w-full bg-[url('/images/bg_one.jpg')] bg-cover bg-center">
      <Navbar />

      <h1 className="text-center font-bold text-black big-text -mt-10">
        DIVINITY
      </h1>

      <div className="relative w-full max-w-6xl mx-auto px-6 -mt-20 flex items-center justify-center">
        <p className="text-center font-bold text-black text-[15px] sm:text-[25px]">
          EXCLUSIVE FOULARD COLLECTION
        </p>
        <button className="absolute right-0 blank-btn">Scroll</button>
      </div>

      <Brandlabels />

      <div className="flex justify-end px-10 mt-17 mb-5">
        <button className="primary-btn">Shop Now</button>
      </div>

      <div className="w-full flex gap-5">
        <div>
          <img
            src="/images/scarf_one.png"
            alt="Scarf Bottom"
            width={600}
            height={600}
          />
        </div>
        <div>
          <img
            src="/images/scarf_two.png"
            alt="Scarf Bottom"
            width={600}
            height={600}
          />
        </div>
        <div>
          <img
            src="/images/scarf_three.png"
            alt="Scarf Bottom"
            width={600}
            height={600}
          />
        </div>
        <div>
          <img
            src="/images/scarf_four.png"
            alt="Scarf Bottom"
            width={600}
            height={600}
          />
        </div>
      </div>
    </div>
  );
}
